.spinner-background{
    background-color: var(--transparent-dark-color);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    opacity: 50%;
}

.spinner {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    z-index: 2;
    position: fixed;
    top:50%;
    left: 50%;

    animation: spinning 1s infinite ease;
}

@keyframes spinning {
    0%,
    100% {
        box-shadow: 0px -41px 0px 0px #ffffff, 29px -29px 0 0px rgba(255, 255, 255, 0.2), 40px 0px 0 0px rgba(255, 255, 255, 0.2), 28px 28px 0 0px rgba(255, 255, 255, 0.2), 0px 40px 0 0px rgba(255, 255, 255, 0.2), -29px 29px 0 0px rgba(255, 255, 255, 0.2), -41px 0px 0 0px rgba(255, 255, 255, 0.5), -29px -29px 0 0px rgba(255, 255, 255, 0.7);
    }

    12.5% {
        box-shadow: 0px -41px 0px 0px rgba(255, 255, 255, 0.7), 29px -29px 0 0px #ffffff, 40px 0px 0 0px rgba(255, 255, 255, 0.2), 28px 28px 0 0px rgba(255, 255, 255, 0.2), 0px 40px 0 0px rgba(255, 255, 255, 0.2), -29px 29px 0 0px rgba(255, 255, 255, 0.2), -41px 0px 0 0px rgba(255, 255, 255, 0.2), -29px -29px 0 0px rgba(255, 255, 255, 0.5);
    }

    25% {
        box-shadow: 0px -41px 0px 0px rgba(255, 255, 255, 0.5), 29px -29px 0 0px rgba(255, 255, 255, 0.7), 40px 0px 0 0px #ffffff, 28px 28px 0 0px rgba(255, 255, 255, 0.2), 0px 40px 0 0px rgba(255, 255, 255, 0.2), -29px 29px 0 0px rgba(255, 255, 255, 0.2), -41px 0px 0 0px rgba(255, 255, 255, 0.2), -29px -29px 0 0px rgba(255, 255, 255, 0.2);
    }

    37.5% {
        box-shadow: 0px -41px 0px 0px rgba(255, 255, 255, 0.2), 29px -29px 0 0px rgba(255, 255, 255, 0.5), 40px 0px 0 0px rgba(255, 255, 255, 0.7), 28px 28px 0 0px #ffffff, 0px 40px 0 0px rgba(255, 255, 255, 0.2), -29px 29px 0 0px rgba(255, 255, 255, 0.2), -41px 0px 0 0px rgba(255, 255, 255, 0.2), -29px -29px 0 0px rgba(255, 255, 255, 0.2);
    }

    50% {
        box-shadow: 0px -41px 0px 0px rgba(255, 255, 255, 0.2), 29px -29px 0 0px rgba(255, 255, 255, 0.2), 40px 0px 0 0px rgba(255, 255, 255, 0.5), 28px 28px 0 0px rgba(255, 255, 255, 0.7), 0px 40px 0 0px #ffffff, -29px 29px 0 0px rgba(255, 255, 255, 0.2), -41px 0px 0 0px rgba(255, 255, 255, 0.2), -29px -29px 0 0px rgba(255, 255, 255, 0.2);
    }

    62.5% {
        box-shadow: 0px -41px 0px 0px rgba(255, 255, 255, 0.2), 29px -29px 0 0px rgba(255, 255, 255, 0.2), 40px 0px 0 0px rgba(255, 255, 255, 0.2), 28px 28px 0 0px rgba(255, 255, 255, 0.5), 0px 40px 0 0px rgba(255, 255, 255, 0.7), -29px 29px 0 0px #ffffff, -41px 0px 0 0px rgba(255, 255, 255, 0.2), -29px -29px 0 0px rgba(255, 255, 255, 0.2);
    }

    75% {
        box-shadow: 0px -41px 0px 0px rgba(255, 255, 255, 0.2), 29px -29px 0 0px rgba(255, 255, 255, 0.2), 40px 0px 0 0px rgba(255, 255, 255, 0.2), 28px 28px 0 0px rgba(255, 255, 255, 0.2), 0px 40px 0 0px rgba(255, 255, 255, 0.5), -29px 29px 0 0px rgba(255, 255, 255, 0.7), -41px 0px 0 0px #ffffff, -29px -29px 0 0px rgba(255, 255, 255, 0.2);
    }

    87.5% {
        box-shadow: 0px -41px 0px 0px rgba(255, 255, 255, 0.2), 29px -29px 0 0px rgba(255, 255, 255, 0.2), 40px 0px 0 0px rgba(255, 255, 255, 0.2), 28px 28px 0 0px rgba(255, 255, 255, 0.2), 0px 40px 0 0px rgba(255, 255, 255, 0.2), -29px 29px 0 0px rgba(255, 255, 255, 0.5), -41px 0px 0 0px rgba(255, 255, 255, 0.7), -29px -29px 0 0px #ffffff;
    }
}